@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200..900;1,200..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap");

:root {
  --margin: 24px;
}

body,
html {
  font-family: "Montserrat", sans-serif;
  background-size: 300% 300%;
  background: var(--bg-gradient);
  animation: waveBackground 30s infinite alternate ease-in-out;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.classicBg {
  --bg-gradient: radial-gradient(#3e709c, #674da9, #3e709c);
  background-size: 300% 300%;
  animation: waveBackground 30s infinite alternate ease-in-out;
}

.nydBg {
  --bg-gradient: radial-gradient(#a5d296, #7aa581, #347b52);
  background-size: 300% 300%;
  animation: waveBackground 30s infinite alternate ease-in-out;
}

h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 128px;
  font-weight: bold;
}

h2 {
  font-size: 64px;
  font-weight: bold;
}

h3 {
  font-size: 24px;
  font-weight: bold;
}

h4 {
  font-size: 18px;
}

.App{
  margin: 36px;
}

.App-link {
  color: #61dafb;
}

.title {
  color: white;
}

.sub {
  color: white;
  margin-top: 40vh;
}

.box {
  display: flex;
  align-items: baseline;
  justify-content: flex-start;
  color: white;
  gap: 10px;
  margin-bottom: 50px;
}

.text {
  position: fixed;
  color: white;
  bottom: 64px;
}

@keyframes waveBackground {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@media screen and (max-width: 768px) {
  .App{
    margin: 12px;
  }

  h1 {
    font-size: 32px;
  }

  h2 {
    font-size: 32px;
  }

  h3 {
    font-size: 18px;
  }

  h4 {
    font-size: 12px;
  }

  .text {
    padding-bottom: 20px;
    font-size: 20px;
  }

  .sub {
    margin-top: 50vh;
  }

  .text {
    bottom: var(--margin);
  }
}
